import React from 'react'
import { useState } from 'react'

function Textform(props) {
  const [text, setText] = useState('Enter Text here');

  const handleUpClick = () => {
    // console.log('Uppercase was clicked')
    let newText = text.toUpperCase();
    setText(newText);
    props.showAlert('Converted to Uppercase','success');
  }

  const handleOnChange = (e) => {
    console.log(e);
    setText(e.target.value);
  }

  const handleLoClick = () => {
    //console.log('Lowercase button was clicked');
    let newText = text.toLowerCase();
    setText(newText);
    props.showAlert('Converted to Lowercase','success');
  }

  const handleClearClick = () => {
    //console.log('Button cleartext was clicked');
    let newText = ' ';
    setText(newText);
    props.showAlert('Text cleared','success');
  }

  return (
    <>
      <div className={`container my-3 text-${props.mode==='light'?'black':'white'}`} >
        <h1>{props.heading}</h1>
        <div className="mb-3">
          <textarea className={`form-control text-${props.mode==='light'?'black':'white'}` }  value={text} style={{backgroundColor: props.mode==='light'?'white':'grey'}} onChange={handleOnChange} id="myBox" rows="8"></textarea>
        </div>

        <button className="btn btn-primary" onClick={handleUpClick}>Convert to Uppercase</button>
        <button className="btn btn-primary mx-4" onClick={handleLoClick}>Convert to Lowercase</button>
        <button className="btn btn-primary" onClick={handleClearClick}>Clear Text</button>
      </div>

      <div className={`container my-2 text-${props.mode==='light'?'black':'white'}`}>
          <h1>Your Text summary</h1>
          <p>{text.split(' ').length} words and {text.length} characters</p>
          <p>{0.008 * text.split(' ').length} Minutes to read this</p>
          <h2>Preview</h2>
          <p>{text.length>0 ?text:'Enter something in the textbox above to preview it here'}</p>
      </div>
    </>
  )
}

export default Textform
